@mixin small {
  @media (min-width: 600px) {
    @content;
  }
}

@mixin medium {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin large {
  @media (min-width: 992px) {
    @content;
  }
}

@mixin xl {
  @media (min-width: 1100px) {
    @content;
  }
}

@mixin xxl {
  @media (min-width: 1345px) {
    @content;
  }
}

@mixin max {
  @media (max-width: 768px) {
    @content;
  }
}
